body {
  margin: 0;
  font-family: 'Roboto';
  background-color: #212121;
}

.logo-font {
  font-family: 'Couture' sans-serif;
}

.main-container {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  background: url('img/frontview.jpg') center no-repeat;
  background-size: cover;

  display: flex;
  text-align: center;
}

.main-container .container-opacity {
  min-width: 100%;
  min-height: 100%;
  backdrop-filter: blur(6px);

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.main-container .container-opacity .logo {
  max-width: 500px;
}

.main-container .container-opacity .content {
  margin-top: 30px;
}

.main-container .container-opacity .content > * {
  margin-right: 30px;
}

.page {
  position: absolute;
  width: 100%;
  min-height: 100%;
  color: #FFF;
  display: flex;
  align-items: center;
  justify-content: center;
}

.page .logo {
  margin-top: 10px;
  margin-bottom: 10px;
  width: 200px;
}

.text-block {
  padding: 10px !important;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}

.email-container,
.map-container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

.email-container h1,
.map-container h1 {
  font-size: 40px;
}

.email-container .email {
  font-size: 25px;
  color: #FFF;
}

.page-header {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.page-header > * {
  margin-right: 20px;
}

.map {
  width: 600px;
  height: 400px;
}

@media only screen and (max-width: 768px) {
  .page-header {
    flex-direction: column;
  }

  .map {
    width: 90%;
  }

  .main-container .container-opacity .logo {
    width: 90%;
  }

  .email-container .email {
    font-size: 18px;
  }
}